<template>
  <div class="products" id="products">
      <div class="container">
          <h1 class="text-center p-5">Our Pro<span style="color: #C82333">ducts List</span> </h1>
          <div class="row">
              
              <div class="col-sm-6 col-md-3" v-for="(product, index) in products" :key="index">

                   <div class="modal fade"  id="description" tabindex="-1" role="dialog" aria-labelledby="exampleModalCenterTitle" aria-hidden="true">
                          <div class="modal-dialog modal-dialog-centered" role="document">
                            <div class="modal-content black-bg">
                              <div class="modal-header">
                                <h5 class="modal-title" id="exampleModalCenterTitle">{{descriptionName}}</h5>
                                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                  <span aria-hidden="true">&times;</span>
                                </button>
                              </div>
                              <div class="modal-body">
                                <div class="container">
                                  <div class="row">
                                    <div class="col-md-6">
                                      <carousel :perPage="1">
                                        <slide v-for="(image, index) in descriptionImages" :key="index">
                                              <img :src="image" class="card-img-top card-top-height img-fluid no-padding"  alt="..." width="250px">
                                        </slide>
                                      </carousel>
                                    </div>
                                    <div class="col-md-6">
                                        <div class="col-md-12">Price: &#8358;{{ descriptionprice }}</div>
                                        <div style="" v-html="descriptionDes" class="col-md-12 mt-3"></div>
                                    </div>
                                    
                                  </div>
                                </div>
                                
                              </div>
                              <div class="modal-footer">
                                <button type="button" class="btn-danger btn" data-dismiss="modal">Close</button>
                              </div>
                            </div>
                          </div>
                  </div>
                  <div class="card product-item my-5">

                        <carousel :perPage="1">
                          <slide v-for="(image, index) in product.images" :key="index">
                                <img :src="image" class="card-img-top card-top-height img-fluid no-padding"  alt="..." width="250px">
                          </slide>
                        </carousel>
                
                        <div class="card-body body">
                          <div class="justify-content-between d-flex">
                            <h5 class="card-title">{{ product.name }}</h5>
                            <h5 class="card-priceS">&#8358; {{ product.price}}</h5>
                            
                          </div>
                          <div class="row">
                            <div class="col-md-6"><button class=" btn-dark btn" @click="showDes(product)">Description</button></div>
                            <div class="col-md-6">
                                <add-to-cart
                                :image="getImage(product.images)"
                                :productId="product.id"
                                :price="product.price"
                                :name="product.name"
                                :description="product.description">
                                </add-to-cart>
                               </div>
                          </div>
                          
                          
                          
                           
                            <!-- <add-to-cart 
                                :image="getImage(product.images)"
                                :p-id="product.id"
                                :price="product.price"
                                :name="product.name">
                            </add-to-cart> -->
                        </div>

                      
                    </div>

                   
              </div>

          </div>
      </div>
<!-- Modal -->

    
  </div>
</template>

<script>
import {db} from '../firebase';
import $ from 'jquery'


export default {
  name: "Productlist",
  props: {
    msg: String
  },

data(){
    return {
        products: [],
        descriptionName: '',
        descriptionprice: '',
        descriptionDes: '',
        descriptionImages: []
     
    }
  },

  methods:{
    showDes (p) {
      // console.log(p.description)
      this.descriptionName = p.name
      this.descriptionprice = p.price
      this.descriptionDes = p.description
      this.descriptionImages = p.images
      $('#description').modal('show')
    },

    getImage(images){
      return images[0];
      // Since we cannot pass an array as a prop, we need to use a function
    }

  },

  firestore(){
      return {
        products: db.collection('products'),
      }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    .products{
        margin-top: 2rem;
        background: black;
        padding-bottom: 3rem;
    }
    .product-item {
      background: rgb(22, 22, 22);
      color: #C82333;
      box-shadow: 3px 3px 10px #C82333;
      // height: 400px;
    }
    .card-top-height {
      height: 250px;
      padding: 2px;
    }
    .body {
      padding: 0 10px 10px 10px;
    }
    .no-padding {
      padding: 0;
    }
    p {
      font-size: 10px;
    }
    .black-bg {
      background-color: black;
      color: white;
    }
    button {
      margin: 10px 5px;
      outline: none;
      font-size: 10px;
    }

</style>
